import React from "react";
import { useRecoilValue, useRecoilState } from "recoil";
import styled from "styled-components";
import { Helmet } from "react-helmet-async";
import {
  ICategory,
  categoryState,
  categoriesState,
  toDoSelector,
} from "../atoms";
import CreateCategory from "./CreateCategory";
import CreateToDo from "./CreateToDo";
import ToDo from "./ToDo";

const Wrapper = styled.div`
  width: 40vw;
  margin: 50px auto;
`;

const Upper = styled.div`
  height: 50px;
  margin: 0 auto;
`;
const Title = styled.h1`
  text-align: center;
  font-size: 20px;
`;

const Box = styled.div`
  width: 500px;
  padding: 10px;
  text-align: center;
  margin: 10px auto;
  border-radius: 10px;
  background-color: #ead0df;
`;

function ToDoList() {
  const toDos = useRecoilValue(toDoSelector);
  const [defaultCategory, setCategory] = useRecoilState(categoryState);
  const categories = useRecoilValue(categoriesState);
  const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
    setCategory({ title: event.currentTarget.value } as ICategory);
  };
  return (
    <>
      <Helmet>
        <title>To-Do List</title>
      </Helmet>

      <Wrapper>
        <Upper>
          <Title>Make your To-Do List</Title>
        </Upper>
        <Box>
          <select value={defaultCategory.title} onInput={onInput}>
            {categories?.map((category) => (
              <option key={category.id} value={category.title}>
                {category.title}
              </option>
            ))}
          </select>
          <CreateCategory />
          <CreateToDo />
        </Box>
        <Box>
          {toDos?.map((toDo) => {
            return <ToDo key={toDo.id} {...toDo} />;
          })}
        </Box>
      </Wrapper>
    </>
  );
}

export default ToDoList;
